import { useEffect, useRef, useState } from "react";
import ReactModal, { Props } from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { GetType, GetTypeActiveSpecifications, GetTypeDeletedSpecificationsType } from "../../../../Redux/Admin/Types/TypesSelector";
// Thunks
import { getTypeThunk, getUnusedSpecifications, switchChangeThunk, uploadImageThunk } from "../../../../Redux/Admin/Types/TypesThunk";
// useSelector
import { GetAccessToken } from "../../../../Redux/Auth/AuthSelector";
// Styles
import styles from './TypeSpecificationsInfoModal.module.css';
import { TranslationType, TypesActions, TypeSpecificationType, TypeType } from "../../../../Redux/Admin/Types/TypesReducer";
import ReactSwitch from "react-switch";
import { Exit } from "../../../../Components/Exit/Exit";
import { yupResolver } from '@hookform/resolvers/yup';
import { AddSpecificationModal } from "../../../../Components/Modal/SpecificationsPage/AddSpecificationModal/AddSpecificationModal";
import { useForm } from "react-hook-form";
import * as yup from 'yup';
import toast from "react-hot-toast";
import { Button } from "@mui/material";
import { ActiveTypeSpecifications } from "../ActiveTypeSpecifications/ActiveTypeSpecifications";
import { DeletedTypeSpecifications } from "../DeletedTypeSpecifications/DeletedTypeSpecifications";
import { AddSpecificationToType } from "../AddSpecificationToType/AddSpecificationToType";
import { PagesLoader } from "../../../Loader/PagesLoader/PagesLoader";

type PropsType = {
    changeModalOpen: (bool: boolean) => void;
    isModalOpen: boolean;
    id: number;
    nextModalOpen: (bool: boolean) => void;
}

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      width: '100vw',
      height: '100vh',
      background: 'transparent',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };

export const TypeSpecificationsInfoModal = (props: PropsType) => {
    const dispatch = useDispatch();

    // Local state
    const [is_loading, set_is_loading] = useState(false);
    const [file, setFile] = useState<any>();
    const inputRef = useRef<HTMLInputElement | null>(null);
    const [add_specification_modal_shown, set_add_specification_modal_shown] = useState(false);

    // Global state
    const access_token = useSelector(GetAccessToken);
    const type: TypeType = useSelector(GetType);
    const typeActiveSpecifications = useSelector(GetTypeActiveSpecifications);
    const typeDeletedSpecifications = useSelector(GetTypeDeletedSpecificationsType);

    // Functions
    const UploadContent = (event:any) => {
        if (event.currentTarget.files[0].size >= 1000000) {
            return toast.error('Suratyň ululygy 1 megabaýtdan köp bolmaly däl');
        }
        event.preventDefault();
        if (event.target.files[0]) {
            setFile(event.target.files[0]);
        }
    };

    const OnImageFormSumbit = () => {
        const formData = new FormData();
        formData.append('picture', file);
        dispatch(uploadImageThunk(props.id, formData, access_token))
    }

    // Initial actions
    useEffect(() => {
        set_is_loading(true);
        dispatch(TypesActions.clearWannaUseSpecs());
        dispatch(getTypeThunk({id: props.id, changeIsLoading: set_is_loading, access_token}))
    }, [access_token, dispatch]);
    
    return (
        <ReactModal portalClassName={styles.modal_portal} style={customStyles}  isOpen={props.isModalOpen}>
            <div className={styles.modal}>
                <Exit changeModalOpen={() => props.changeModalOpen(false)}/>
                {is_loading
                    ? <PagesLoader />
                    : <div className={styles.content_block}>
                        {type
                            ? <>
                                <h2 className={styles.absolute_name}>{type.absolute_name}</h2>
                                {type.destination 
                                    ? <img className='w-40 h-40' src={`${process.env.REACT_APP_BASE_URL}/${type.destination}`}/> 
                                    : null
                                }
                                <div className={styles.change_image_block}>
                                <label htmlFor="contained-button-file">
                                    <input className='hidden' onChange={UploadContent} accept="image/png, image/jpeg" id="contained-button-file" multiple type="file" />
                                    <Button variant="contained" component="span">
                                        Upload
                                    </Button>
                                </label>
                                    {/* <input
                                        accept='image/png, image/jpeg'
                                        multiple
                                        type="file"
                                        name='picture'
                                        onChange={UploadContent}
                                    /> */}
                                    <Button color='success' variant='contained' onClick={OnImageFormSumbit}>{type.destination ? 'Сменить' : 'Отправить'}</Button>
                                        
                                </div>
                                <div className={styles.specifications_block}>
                                    {typeActiveSpecifications
                                        ? 
                                        <div className={styles.active_specifications_block}>
                                            <ActiveTypeSpecifications activeSpecifications={typeActiveSpecifications}/>
                                        </div>
                                        : null
                                    }
                                    {typeDeletedSpecifications
                                        ?
                                        <div className={styles.deleted_specifications_block}>
                                            <DeletedTypeSpecifications deleteSpecifications={typeDeletedSpecifications} />
                                        </div>
                                        : null
                                    }
                                </div>
                                <button onClick={() => {props.nextModalOpen(true); props.changeModalOpen(false)}}>
                                    Добавить спецификацию
                                </button>
                            </>
                        : null
                    }
                </div>
            }
            </div>
        </ReactModal>
    );
}