import { Button, TextField } from '@mui/material';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getOperatorsThunk } from '../../../Redux/Admin/Operators/OperatorsThunk';
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import { CreateOperatorModal } from '../../Modal/OperatorsPage/CreateOperatorModal/CreateOperatorModal';
import useTimeout from '../../useTimeout/useTimeout';
import styles from './SearchOperators.module.css';


export const SearchOperators = (props: PropsType) => {
    const dispatch = useDispatch();
    
    // Local state
    const access_token = useSelector(GetAccessToken);
    const [name_input_state, setName_input_state] = useState('');
    const [phone_input_state, setPhone_input_state] = useState('');

    // Functions
    const handleSearch = useTimeout({ time: 1500, action: (value: any) => {props.changeIsLoading(true); dispatch(getOperatorsThunk('', value.name, value.phone, props.changeIsLoading, access_token))}});
    
    return (
        <>
            <div className={styles.main_block}>
                <div className={styles.content_block}>
                    <TextField variant='filled' label='Название'
                        onChange={(e) => {                               
                            setName_input_state(e.target.value);
                            handleSearch({name: e.target.value, phone: phone_input_state});
                        }}
                    />
                    <TextField variant='filled' label='Номер телефона'
                        onChange={(e) => {
                            setPhone_input_state(e.target.value);
                            handleSearch({name: name_input_state, phone: e.target.value});
                        }}
                    />
                    <Button variant='contained' onClick={() => props.changeCreateOperatorModule(true)}>Добавить оператора</Button>
                </div>
            </div>
        </>
    );
};

// Types
type PropsType = {
    changeIsLoading: (bool: boolean) => void;
    changeCreateOperatorModule: (bool: boolean) => void;
}