import { AxiosResponse } from 'axios';
import toast from 'react-hot-toast';
import { api } from '../../../api';
import { Thunk } from '../../types';
import { OperatorDataType, OperatorsActions, OperatorsActionsType } from "./OperatorsReducer";


export const getOperatorsThunk = 
    (page: number | string, full_name: string, phone: string, isLoading: (bool: boolean) => void, token: string): Thunk<OperatorsActionsType> => 
        async dispatch => {
            try {
                if (token === "" || page === null) return;
                debugger;
                const response: AxiosResponse = await api.getApi({
                    url: `admin/get-all-operators?page=${page}&limit=10&full_name=${full_name}&phone=${phone}`, 
                    token: token, 
                    withCredentials: true,
                    contentType: false
                });
                dispatch(OperatorsActions.changeOperatorsCound(response.data.count));
                dispatch(OperatorsActions.addOperators(response.data.operators));
                isLoading(false);
            } catch (error: any) {
                console.log(error);
                
                debugger;
                console.log(error.response.message);
            }
        };


export const deleteOperatorThunk = (id: number, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            debugger;
            const response = await api.postApi({url: `admin/delete-operator/${id}`, token: token, withCredentials: true, params: {}});
            debugger;
            dispatch(OperatorsActions.deleteOperator(id));
        } catch (error: any) {
            console.log(error);
            
            console.log(error.response.message);
        }
    };
};


export const updateOperatorThunk = (operatorData: {name: string, email: string, phone: number}, id: number, token: string) => {
    debugger;
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            await api.postApi({url: `admin/update-operator/${id}`, token: token, withCredentials: true, params: {full_name: operatorData.name, email: operatorData.email, phone: operatorData.phone}});
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};



export const createOperatorThunk = 
    (operatorData: CreateOperatorDataType, token: string): Thunk<OperatorsActionsType>=> 
        async dispatch => {
            try {
                if (token === "") return;
                const response: {data: {rows: OperatorDataType}} = await api.postApi({url: `admin/add-operator`, token: token, withCredentials: true, params: {full_name: operatorData.full_name, email: operatorData.email, phone: operatorData.phone, password: operatorData.password}});
                dispatch(OperatorsActions.createOperator(response.data.rows));
            } catch (error: any) {
                console.log(error.response);
                debugger;
                toast.error(error.response.data.error[0].message);
            }
        };

// Types

type CreateOperatorDataType = {
    full_name: string;
    email: string;
    phone: string;
    password: string;
};