import Modal from "react-modal"
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Store
import { GetRegionLocations } from "../../../../Redux/Admin/Locations/LocationsSelector";
import { GetAccessToken } from "../../../../Redux/Auth/AuthSelector";
// Components
import { Button } from "@mui/material";
// Thunks
import { getRegionLocationsThunk } from "../../../../Redux/Admin/Locations/LocationsThunk";
// Styles
import styles from './RegionInfoModal.module.css';
import { PagesLoader } from "../../../Loader/PagesLoader/PagesLoader";


export const RegionInfoModal: React.FC<PropsType> = (props) => {
    const dispatch = useDispatch();

    // Local store
    const access_token = useSelector(GetAccessToken);
    const [isLoading, setIsLoading] = useState(false);
    
    // Global store
    const regions = useSelector(GetRegionLocations);
    
    // Initial state
    useEffect(() => {
        setIsLoading(true);
        dispatch(getRegionLocationsThunk(setIsLoading, props.id, access_token))
    }, [access_token, props.id, dispatch]);

    // Functions
    const onAddRegionButtonClick = () => {
        props.changeAddRegionModalOpen(true);
        props.changeModalOpen(false);
    };

    // Render items
    let regionsRender = regions?.map((region: RegionsType) => {
        return <div className={styles.regions}> {region.name} </div>
    });

    return (
        <Modal className={styles.modal} isOpen={props.isModalOpen}>
            {isLoading
                ? <PagesLoader />
                : <div className={`${styles.content_block}`}>
                    <div className={styles.regions_block}>
                        {regionsRender}
                    </div>
                    <div className={styles.button_block}>
                        <Button variant='contained' color='success' onClick={onAddRegionButtonClick}> Добавить регион </Button>
                        <Button variant='contained' color='error' onClick={() => props.changeModalOpen(false)}> Выйти </Button>
                    </div>
                </div>
            }
        </Modal>
    );
}


// Types
type PropsType = {
    changeModalOpen: (bool: boolean) => void;
    changeAddRegionModalOpen: (bool: boolean) => void;
    isModalOpen: boolean;
    id: number;
};

type RegionsType = {
    name: string;
    id: number;
};