import { Button, TextField } from '@mui/material';
import { FormProvider, useForm } from 'react-hook-form';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { updateOperatorThunk } from '../../../../Redux/Admin/Operators/OperatorsThunk';
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
import { Exit } from '../../../Exit/Exit';
import { EmailInput } from '../../../Inputs/EmailInput/EmailInput';
import { NameInput } from '../../../Inputs/NameInput/NameInput';
import {PhoneInput} from '../../../Inputs/PhoneInput/PhoneInput';
import styles from './OperatorEditModal.module.css';

export const OperatorEditModule = (props: {changeModalOpen: (bool: boolean) => void, isModalOpen: boolean, tr: any}) => {
    debugger;
    const access_token = useSelector(GetAccessToken);
    const dispatch = useDispatch();
    type FormType = {
        name: string;
        email: string;
        phone: number;
    }

    const onFormSubmit = (data: any) => {
        dispatch(updateOperatorThunk(data, props.tr[0].id, access_token))
    };
    const methods = useForm<FormType>({
        defaultValues: {
            name: props.tr[0].full_name,
            email: props.tr[0].email,
            phone: props.tr[0].phone,
        }
    });;
    const { handleSubmit, register } = methods;

    return (
        
        <Modal portalClassName={styles.modal_portal} className={styles.modal} isOpen={props.isModalOpen}>
            <div className={styles.content_block}>
            <Exit changeModalOpen={props.changeModalOpen}/>

            <FormProvider {...methods}>
                <form className={styles.form} onSubmit={handleSubmit(onFormSubmit)}>
                    <NameInput />
                    <EmailInput />
                    <div>
                        <TextField label='phone' {...register('phone')}/>
                    </div>
                    <Button variant='contained'>Изменить</Button>
                </form>
                </FormProvider>
            </div>

        </Modal>
    );
}

