import { useDispatch, useSelector } from 'react-redux';
// Thunks
import { getAllSpecificationsThunk } from '../../../../Redux/Admin/Specifications/SpecificatiosThunk';
// Store
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
// Functions
import useTimeout from '../../../useTimeout/useTimeout';
// Modals
import { AddSpecificationModal } from '../../../Modal/SpecificationsPage/AddSpecificationModal/AddSpecificationModal';
// Styles
import styles from './SearchSpecifications.module.css';
import { Button, TextField } from '@mui/material';


export const SearchSpecifications: React.FC<PropsType> = (props) => {
    const dispatch = useDispatch();

    // Global state
    const access_token = useSelector(GetAccessToken);

    // Functions
    const handleSearch = useTimeout({ time: 1500, action: (value: string) => {props.changeIsLoading(true); dispatch(getAllSpecificationsThunk(props.changeIsLoading, access_token, value, 0))}});
    
    return (
        <div className={styles.main_block}>
            <div className={styles.content_block}> 
               <AddSpecificationModal changeModalOpen={props.changeModalOpen} isModalOpen={props.isModalOpen}/>
                <TextField label='Название' variant='filled' onChange={(e) => handleSearch(e.target.value)}/>
                <Button variant='contained' onClick={() => props.changeModalOpen(true)} className={styles.addSpecificationButton}>
                    Spesifikasiýa goşmak
                </Button>
            </div>
        </div>
    );
};

// Types
type PropsType = {
    changeModalOpen: (bool: boolean) => void;
    changeIsLoading: (bool: boolean) => void;
    isModalOpen: boolean;
};