import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Components
import { Header } from "../../../Components/Header/Header";
import { OperatorEditModule } from "../../../Components/Modal/OperatorsPage/OperatorEditModal/OperatorEditModal";
import { SearchOperators } from "../../../Components/Search/SearchOperators/SearchOperators";
// State
import { GetOperators, GetOperatorsCount, GetOperatorsIsLoading, GetSearchedOperators } from "../../../Redux/Admin/Operators/OperatorsSelector";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Thunks
import { deleteOperatorThunk, getOperatorsThunk } from "../../../Redux/Admin/Operators/OperatorsThunk";
// Functions
import { createPages } from "../../../utils/PagesCreator";
// Images
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
// Styles
import styles from './Operators.module.css';
import { OperatorDataType } from "../../../Redux/Admin/Operators/OperatorsReducer";
import { PagesLoader } from "../../../Components/Loader/PagesLoader/PagesLoader";
import { CreateOperatorModal } from "../../../Components/Modal/OperatorsPage/CreateOperatorModal/CreateOperatorModal";
import { Pagination } from "@mui/material";
import { HourglassLoader } from "../../../Components/Loader/HourglassLoader/HourglassLoader";


export const Operators = () => {
    const dispatch = useDispatch();

    // Local store
    const [deletedOperatorId, setDeletedOperatorId] = useState(0);
    const [update_operator_modal_shown, setUpdate_operator_modal_shown] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [createOperatorModalShown, setCreateOperatorModalShown] = useState(false);
    
    const [clickedId, setClickedId] = useState(0);
    const [current_page, setCurrent_page] = useState(1);
    const pages: number[] = [];

    // Global state
    const access_token = useSelector(GetAccessToken);
    let operators: OperatorDataType[] = useSelector(GetOperators);
    const totalCount = useSelector(GetOperatorsCount);

    // Functions
    const pagesCount = Math.round(totalCount/15);
    createPages(pages, pagesCount, current_page);

    const onDeleteButtonClick = (id: number) => {
        debugger;
        setDeletedOperatorId(id)
        dispatch(deleteOperatorThunk(id, access_token));
    };

    useEffect(() => {
        setIsLoading(true);
        dispatch(getOperatorsThunk(current_page, '', '', setIsLoading, access_token));
    }, [access_token, current_page, dispatch]);

    // Render elements
    const operatorsRender = operators?.map((operator: any) => {
        return (
            <div className={styles.operators_block}>
                <div>{operator.full_name}</div>
                <div>{operator.email}</div> 
                <div>{operator.phone}</div>
                <div>{operator.id}</div>
                <div>
                    <DeleteIcon className='text-red-600 cursor-pointer' onClick={() => onDeleteButtonClick(operator.id)} />
                </div>
                <div>
                    <EditIcon className='text-blue-600 cursor-pointer' onClick={() => {setUpdate_operator_modal_shown(true); setClickedId(operator.id)}} />
                </div>
            </div>
        )});

    const headerItems = ['Имя', 'Email', 'Номер', 'ID', 'Удалить', 'Изменить'];
        return (
            <div className={styles.main_block}>
                {update_operator_modal_shown 
                ? <OperatorEditModule
                    tr={operators.filter((operator: OperatorDataType) => Number(operator.id) === Number(clickedId))} 
                    isModalOpen={update_operator_modal_shown}
                    changeModalOpen={setUpdate_operator_modal_shown}/>
                : null
                }
                {createOperatorModalShown
                ? <CreateOperatorModal isModalShown={createOperatorModalShown} setIsModalShown={setCreateOperatorModalShown}/>
                : null
                }   <div>
                        <SearchOperators changeCreateOperatorModule={setCreateOperatorModalShown} changeIsLoading={setIsLoading}/>
                        <div className={styles.content_block}>
                            <Header headerItems={headerItems}/>
                            {isLoading ? <PagesLoader /> : operatorsRender}
                            <Pagination count={pagesCount} shape='rounded' onChange={(e, page) => setCurrent_page(page)} />
                        </div>
                    </div>
            </div>
        )
}