import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from 'react-hook-form';
import Modal from 'react-modal';
// Thunk
import { createSpecificationThunk } from '../../../../Redux/Admin/Specifications/SpecificatiosThunk';
// Components
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
import { Exit } from '../../../Exit/Exit';
// Styles
import styles from './AddSpecificationModal.module.css';
import { Button, TextField } from '@mui/material';


const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      width: '100vw',
      height: '100vh',
      background: 'transparent',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };


type PropsType = {
    isModalOpen: boolean;
    changeModalOpen: (bool: boolean) => void
};

export const AddSpecificationModal = (props: PropsType) => {
    const dispatch = useDispatch();

    // Local state
    const [absolute_values_count, setAbsolute_values_count]: any = useState([]);
    const { register, handleSubmit } = useForm();

    // Global state
    const access_token = useSelector(GetAccessToken);

    // Functions
    const addAbsoluteValue = (data: any) => {
        setAbsolute_values_count([...absolute_values_count, {title: data.target.value, id: Date.now()}]);
    };

    const removeAbsoluteValue = (id: number) => {
        setAbsolute_values_count(absolute_values_count.filter((index: any) => index.id !== id));
    };

    // Types


    let valueTranslations: any = [];
    let absoluteValues: any = [];

    const onFormSubmit = (data: any) => {

        const absoluteValuesCount = absolute_values_count.length;
        for (let i = 0; i <= absoluteValuesCount; i++) {
            valueTranslations.push([
                {lang_id: 1, name: data[`absolute_value_turkmen_${i}`]}, 
                {lang_id: 2, name: data[`absolute_value_russian_${i}`]}
            ]);
            absoluteValues.push(data[`absolute_value_${i}`]);
        };
    
        const preparedData = {
            absolute_name: data.absolute_name,
            is_required: data.absolute_name_isRequired,
            is_multiple: data.absolute_name_isMultiple,
            translations: [
                {lang_id: 1, name: data.absolute_name_turkmen},
                {lang_id: 2, name: data.absolute_name_russian},
            ],
            absolute_values: absoluteValues,
            values_translations: valueTranslations,
        };
        dispatch(createSpecificationThunk(preparedData, access_token));
    };

    return (
        <Modal portalClassName={styles.modal_portal} style={customStyles} isOpen={props.isModalOpen}>
            <div className={styles.main_block}>
                <div className={`${styles.content_block} relative`}>
                    <Exit changeModalOpen={props.changeModalOpen} />
                    <h1 className={styles.title}>Täze spesifikasiýa goşmak</h1>
                    <div className={styles.absolute_name_block}>
                        <TextField className={styles.absolute_name_input} label='Абсолютное название' {...register('absolute_name', {required: true})} />
                        <div className={styles.absolute_name_translations_block}> 
                            <TextField label='На туркменском' {...register('absolute_name_turkmen', {required: true})} />
                            <TextField label='На русском' {...register('absolute_name_russian', {required: true})} />
                        </div>
                        <div className={styles.absolute_name_checkbox_block}>
                            <div> 
                                <input id='isRequired' type='checkbox' {...register('absolute_name_isRequired')} />
                                <label htmlFor='isRequired'>Обязательно</label>
                            </div>
                            <div>
                                <input id='isMultiple' type='checkbox' {...register('absolute_name_isMultiple')} />
                                <label htmlFor='isMultiple'>Много</label>
                            </div>
                        </div>
                    </div>
                    <div className={styles.add_absolute_value_block}>
                        <TextField label='Абсолютное значение' {...register('absolute_value_0', {required: true})} />
                        <TextField label='На туркменском' {...register('absolute_value_turkmen_0')} />
                        <TextField label='На русском' {...register('absolute_value_russian_0')} />
                        <Button variant='contained' onClick={addAbsoluteValue}> Добавить </Button>
                    </div>
                    {absolute_values_count.map((input: any, index: number) => {
                        return (
                            <div className={styles.add_absolute_value_block}>
                                <TextField
                                    label='Абсолютное значение'
                                    {...register(`absolute_value_${index+1}`, {required: true})}
                                />
                                <TextField 
                                    label='На туркменском'
                                    {...register(`absolute_value_turkmen_${index+1}`)}
                                />
                                <TextField 
                                    label='На русском'
                                    {...register(`absolute_value_russian_${index+1}`)}
                                />
                                <Button color='error' variant='contained' onClick={() => removeAbsoluteValue(input.id)}>Удалить</Button>
                            </div>
                        )
                    })}
                    <Button type='submit' variant='contained' color='success' onClick={() => {handleSubmit(onFormSubmit)(); props.changeModalOpen(false)}} className={styles.create_button}>Создать</Button>
                </div>
            </div>
        </Modal>

    )
}