import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Components
import { Header } from "../../../Components/Header/Header";
import { SpecificationEditModal } from "../../../Components/Modal/SpecificationsPage/SpecificationEditModal/SpecificationEditModal";
import { SearchSpecifications } from "../../../Components/Search/SearchSpecifications/SearchOperators/SearchSpecifications";
// Thunks
import { changeSpecificationActivationThunk, getAllSpecificationsThunk } from "../../../Redux/Admin/Specifications/SpecificatiosThunk";
// State
import { GetSpecifications, GetSpecificationsTotalCount } from "../../../Redux/Admin/Specifications/SpecificationsSelector";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Images
import spinner from '../../../Images/spinner.gif';
import packman from '../../../Images/packman.gif';
import EditIcon from '@mui/icons-material/Edit';
// Types
import { SpecificationsDataType } from "../../../Redux/Admin/Specifications/SpecificationsReducer";
// Functions
import { createPages } from "../../../utils/PagesCreator";
// Styles
import styles from './Specifications.module.css';
import { Pagination, Switch } from "@mui/material";
import { PagesLoader } from "../../../Components/Loader/PagesLoader/PagesLoader";


export const Specifications = () => {
    const dispatch = useDispatch();

    // Local store
    const [isLoading, setIsLoading] = useState(false);
    const [clickedSpecificationId, setClickedSpecificationId] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [switchLoadingId, setSwitchLoadingId] = useState(0);
    const [changeSpecificationModalShown, setChangeSpecificationModalShown] = useState(false);
    const [addSpecificationModalShown, setAddSpecificationModalShown] = useState(false);
    let pages: number[] = [];
    const headerItems = ['ID', 'Абсолютное название', 'На туркменском', 'На русском', 'Обязательно', 'Несколько', 'Активация', 'Изменить'];

    // Global store
    const access_token = useSelector(GetAccessToken);
    const specifications = useSelector(GetSpecifications);
    const totalCount = useSelector(GetSpecificationsTotalCount);

    // Functions
    const onSpecificationActivationChange = (id: number, boolValue: boolean) => {
        setSwitchLoadingId(id);
        dispatch(changeSpecificationActivationThunk(id, !boolValue, setSwitchLoadingId, access_token));
    };

    const onPaginationChange = (e: React.ChangeEvent<unknown>, page: number) => {
        setCurrentPage(page);
    }

    const pagesCount = Math.round(totalCount/15);
    createPages(pages, pagesCount, currentPage);
    
    // Initial actions
    useEffect(() => {
        setIsLoading(true);
        dispatch(getAllSpecificationsThunk(setIsLoading, access_token, '', currentPage - 1));
    }, [access_token, dispatch, currentPage]);

    const specification = specifications?.map((specification: SpecificationsDataType) => {
        return (
            <div className={styles.specifications_block}>
                <div>{specification.id}</div>
                <div>{specification.absolute_name}</div>
                <div>
                    {specification.translations
                        .filter((translation: TranslationsType) => translation.language_id === 1)
                        .map((translation: TranslationsType) => translation.name)
                    }
                </div>
                <div>
                    {specification.translations
                        .filter((translation: TranslationsType) => translation.language_id === 2)
                        .map((translation: TranslationsType) => translation.name)
                    }
                </div>
                <div> {(specification.is_required) ? 'Dogry' : 'Ýalan'} </div>
                <div> {specification.is_multiple ? 'Dogry' : 'Ýalan'} </div>
                {(switchLoadingId === specification.id)
                    ? <PagesLoader />
                    : <Switch
                        checked={specification.is_active}
                        onChange={() => onSpecificationActivationChange(specification.id, specification.is_active)} />
                    }
                <EditIcon className={styles.edit_button} onClick={() => {setClickedSpecificationId(specification.id); setChangeSpecificationModalShown(true)}}>Üýtgetmek</EditIcon>
            </div>
        );
    });

    
    return (
        <div className={styles.main_block}>
            
                <div>
                    <SearchSpecifications changeIsLoading={setIsLoading} isModalOpen={addSpecificationModalShown} changeModalOpen={setAddSpecificationModalShown}/>
                    <Header headerItems={headerItems}/>
                    <div>
                    {isLoading 
                        ? <PagesLoader />
                        : 
                        <>
                            {specification}
                            {(clickedSpecificationId !== 0)
                                ? <SpecificationEditModal
                                    isModalOpen={changeSpecificationModalShown} 
                                    changeModalOpen={setChangeSpecificationModalShown}
                                    id={clickedSpecificationId} />
                                : null}
                            <Pagination count={pagesCount} shape='rounded' onChange={onPaginationChange} />
                        </>
                        }
                    </div>
                </div>
        </div>
    );
}


// Types
type TranslationsType = {
    language_id: number;
    name: string;
}