import { useEffect } from 'react';
import { useHistory } from 'react-router';
import {Pie} from 'react-chartjs-2'; 
import { useDispatch, useSelector } from 'react-redux';
// Components
import { AnalyticsFilter } from '../../../../Components/Filter/AnalyticsFilter/AnalyticsFilter';
// Thunk
import { getAnalyticsDataThunk } from '../../../../Redux/Admin/Analytics/AnalyticsThunk';
// Store
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
import { GetAnalyticsData } from '../../../../Redux/Admin/Analytics/AnalyticsSelector';
// Styles
import styles from './EstatesAnalatics.module.css';
// Helpers
import { SearchNamesById } from '../../../../utils/SearchNamesById';
// Types
import { AnalyticsData } from '../../../../Redux/Admin/Analytics/AnalyticsReducer';
// Images
import arrow from '../../../../Images/right-arrow.png';


export const Analytics = () => {
    const dispatch = useDispatch();
    const history = useHistory();

    // Global state
    const analytics: AnalyticsData[] = useSelector(GetAnalyticsData);    
    const access_token: string = useSelector(GetAccessToken);
    const analyticsTitle: string[] = [];
    const labelsArray: any[] = [];
    
    // Initial actions
    useEffect(() => {
        dispatch(getAnalyticsDataThunk({}, access_token));
    }, [access_token, dispatch]);

    if (analytics) {
        SearchNamesById(analytics, analyticsTitle);
    }

    
    return (
        <div className={styles.main_block}>
            <AnalyticsFilter />
            {analytics
                ? <div className={styles.analytics_block}>
                    <img src={arrow} alt='Arrow' onClick={() => {history.push('/admin/analytics/price')}} className={styles.arrow_image}/>
                    <Pie
                        width='400px'
                        height='500px'
                        data={{
                            labels: labelsArray,
                            datasets: [{
                                backgroundColor: [
                                    'rgba(255, 99, 132, 0.2)',
                                    'rgba(54, 162, 235, 0.2)',
                                    'rgba(255, 206, 86, 0.2)',
                                    'rgba(75, 192, 192, 0.2)',
                                ],
                                borderColor: [
                                    'rgba(255, 99, 132, 1)',
                                    'rgba(54, 162, 235, 1)',
                                    'rgba(255, 206, 86, 1)',
                                    'rgba(75, 192, 192, 1)',
                                ],
                                label: 'GESE',
                                data: [analytics[0].count, analytics[1].count, analytics[2].count, analytics[3].count]
                            }]
                        }} redraw={true}/>
                </div>
                : <div>Hello</div>
            }
            
        </div>
    );
};


