import { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ReactModal from 'react-modal';
import ImageUploading from 'react-images-uploading';
// Components
import { Exit } from '../../../Exit/Exit';
// State
import { GetLocationImages } from '../../../../Redux/Admin/Pictures/ImagesSelector';
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
// Types
import { LocationImagesType } from '../../../../Redux/Admin/Pictures/ImagesReducer';
// Thunks
import { getImagesLocationThunk, uploadImageThunk } from '../../../../Redux/Admin/Pictures/ImagesThunk';
// Styles
import styles from './ChangeImagesModal.module.css';
import { Button } from '@mui/material';
import toast from 'react-hot-toast';
import ImageUpload from '../../../Upload/ImageUpload/ImageUpload';


export const ChangeImagesModal: React.FC<PropsType> = (props) => {
    const dispatch = useDispatch();

    // Global store
    const access_token = useSelector(GetAccessToken);
    const images = useSelector(GetLocationImages);

    // Local store
    const [image, setImage] = useState<any>();
    const [preview, setPreview] = useState<string | null>();
    console.log(image);
    
    const fileInputRef = useRef<any>();

    //  Functions
    const onDeleteImageButtonClick = () => {
        setPreview(null);
    }

    const onUploadImageChange = (event: any) => {
        if (event.target.files[0].size > 1500000) {
            toast.error('Файл не должен превышать 1.5 мегабайта');
        } else {
            const file = event.target.files[0];
            if (file && file.type.substr(0, 5) === 'image') {
                setImage(file);
            } else {
                setImage(null);
            }
        }
    };

    useEffect(() => {
        if (image) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setPreview(reader.result as string);
            }
            reader.readAsDataURL(image);
        } else {
            setPreview('');
        }
    }, [image]);

    const imageSentClick = () => {
        const data = new FormData();
        data.append('picture', image);
        setImage(null);
        dispatch(uploadImageThunk(props.id, data, access_token));
    }

    // Render element
    let imagesRender = images?.map((image: LocationImagesType) => (
        <img src={`${process.env.REACT_APP_BASE_URL}/${image.destination}`} className={styles.images} />
    ));  

    // Initial actions
    useEffect(() => {
        dispatch(getImagesLocationThunk(props.id, access_token));
    }, [access_token, dispatch, props.isModalOpen]);


    return (
        <ReactModal className={styles.modal} isOpen={props.isModalOpen}>

            <div className={styles.main_block}>
            <Exit changeModalOpen={() => props.changeModalOpen(false)} />
                
                {(imagesRender?.length !== 0) 
                    ? <>{imagesRender}</>
                    : <div className='text-center'>Картинок нет</div>
                }
                
                <form className={styles.form}>
                    {preview
                        ?
                            <div>
                                <img src={preview} />
                                <div className='cursor-pointer' onClick={onDeleteImageButtonClick}>Удалить</div>
                            </div>

                        : <Button variant='contained' onClick={(event) => {
                            event.preventDefault();
                            fileInputRef.current?.click();
                        }}>{images ? 'Сменить' : 'Добавить'}</Button>
                    }
                    <div className={styles.button_block}>
                        <input 
                            type='file'
                            accept='image/jpg, image/png, image/jpeg'
                            style={{display: 'none'}}
                            ref={fileInputRef}
                            onChange={onUploadImageChange} />
                        <Button color='success' onClick={imageSentClick} variant='contained'>Отправить</Button>
                    </div>
                </form>
            </div>
        </ReactModal>
    )
}


// Types
type PropsType = {
    isModalOpen: boolean;
    changeModalOpen: (bool: boolean) => void;
    id: number
};