import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Components
import { AddMainLocationModal } from "../../../Components/Modal/LocationPage/AddMainLocationModal/AddMainLocationModal";
import { RegionInfoModal } from "../../../Components/Modal/LocationPage/RegionInfoModal/RegionInfoModal";
// Store
import { GetMainLocations } from "../../../Redux/Admin/Locations/LocationsSelector";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Thunks
import { getMainLocationsThunk } from "../../../Redux/Admin/Locations/LocationsThunk";
// Images
import packmanLoader from '../../../Images/packman.gif';
// Styles
import styles from './Locations.module.css';
import { AddRegionModal } from "../../../Components/Modal/LocationPage/AddRegionModal/AddRegionModal";
import { Button } from "@mui/material";
import { PagesLoader } from "../../../Components/Loader/PagesLoader/PagesLoader";


export const Locations = () => {
    const dispatch = useDispatch();

    // Local store
    const access_token = useSelector(GetAccessToken);
    const [isLoading, setIsLoading] = useState(false);
    const [addRegionModalShown, setAddRegionModalShown] = useState(false);
    const [regionInfoModalShown, setRegionInfoModalShown] = useState(false);
    const [clickedId, setClickedId] = useState(0);
    const [addMainLocationModalShown, setAddMainLocationModalShown] = useState(false);

    // Global store
    const mainLocations = useSelector(GetMainLocations);

    // Initial actions
    useEffect(() => {
        setIsLoading(true);
        dispatch(getMainLocationsThunk(setIsLoading, access_token));
    }, [access_token, dispatch]);

    // Render Items
    mainLocations?.map((mainLocation: MainLocationsType) => (
        <div onClick={() => {onMainLocationClick(mainLocation.id)}} className={styles.main_location}>
            {mainLocation.name}
        </div>
    ))

    // Functions
    const onMainLocationClick = (id: number) => {
        setRegionInfoModalShown(true); 
        setClickedId(id);
    }

    return (
        <div className={styles.main_block}>
            {isLoading
                ? <PagesLoader />
                : <div className={styles.content_block}>
                    {clickedId 
                        ? <RegionInfoModal
                            id={clickedId}
                            changeAddRegionModalOpen={setAddRegionModalShown}
                            isModalOpen={regionInfoModalShown} 
                            changeModalOpen={setRegionInfoModalShown}/> 
                        : null
                    }
                    {addRegionModalShown
                        ? <AddRegionModal 
                            id={clickedId}
                            isModalOpen={addRegionModalShown}
                            changeModalOpen={setAddRegionModalShown}
                        />
                        : null
                    }
                    {mainLocations.map((mainLocation: any) => (
                        <div 
                            onClick={() => {setRegionInfoModalShown(true); 
                            setClickedId(mainLocation.id)}} 
                            className={styles.main_location}>
                                {mainLocation.name}
                        </div>
                    ))}
                    <AddMainLocationModal isModalOpen={addMainLocationModalShown} changeModalOpen={setAddMainLocationModalShown}/>
                    <Button variant='contained' color='success' onClick={() => {setAddMainLocationModalShown(true)}}> 
                        Добавить новую локацию 
                    </Button>
                </div>
                }
        </div>
    );
};


// Types
type MainLocationsType = {
    id: number;
    name: string;
};