import { useDispatch, useSelector } from 'react-redux';
import { FormProvider, useForm } from 'react-hook-form';
import Modal from 'react-modal';
// State
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
// Components
import { EmailInput } from '../../../Inputs/EmailInput/EmailInput';
import { NameInput } from '../../../Inputs/NameInput/NameInput';
import { PasswordInput } from '../../../Inputs/PasswordInput/PasswordInput';
import { PhoneInput } from '../../../Inputs/PhoneInput/PhoneInput';
import { Exit } from '../../../Exit/Exit';
// Thunks
import { createOperatorThunk } from '../../../../Redux/Admin/Operators/OperatorsThunk';
// Styles
import styles from './CreateOperatorModal.module.css';
import { Button } from '@mui/material';




export const CreateOperatorModal: React.FC<PropsType> = (props) => {
    const dispatch = useDispatch();
    
    // Local state
    const access_token = useSelector(GetAccessToken);
    const methods = useForm<FormType>();
    const { handleSubmit } = methods;

    // Functions
    const onFormSubmit = (data: FormType) => {
        data.phone = data.phone.substr(7);
        dispatch(createOperatorThunk(
            {full_name: data.name, phone: data.phone, email: data.email, password: data.password}, 
            access_token
        ));
        props.setIsModalShown(false);
    }


    return (
        <Modal portalClassName={styles.modal_portal} className={styles.main_block} isOpen={props.isModalShown}>
            <FormProvider {...methods}>

                <div onClick={() => console.log('JEOD)E')} className={styles.content_block}>
                <h1 className={styles.title}>Добавить оператора</h1>

                <Exit changeModalOpen={() => props.setIsModalShown(false)}/>
                    <form onSubmit={handleSubmit(onFormSubmit)}>
                        <div className={styles.inputs_block}>
                            <NameInput />
                            <PasswordInput />
                            <PhoneInput />
                            <EmailInput />
                        </div>
                        <Button type='submit' variant='contained'>Добавить</Button>
                    </form>
                </div>
            </FormProvider>
        </Modal>
    );
};


// Types
type PropsType = {
    isModalShown: boolean;
    setIsModalShown: (value: boolean) => void;  
};

type FormType = {
    name: string;
    phone: string;
    password: string;
    email: string;
};
