import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Components
import { ChangeImagesModal } from "../../../Components/Modal/ImagesPage/ChangeImagesModal/ChangeImagesModal";
// State
import { GetImagesLocations } from "../../../Redux/Admin/Pictures/ImagesSelector";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Thunks
import { getImagesLocationsThunk } from "../../../Redux/Admin/Pictures/ImagesThunk";
// Styles
import styles from './Images.module.css';


export const Pictures = () => {
    const dispatch = useDispatch();

    // Local state
    const access_token = useSelector(GetAccessToken);
    const [changeImageModalShown, setChangeImageModalShown] = useState(false);
    const [clicked_id, setClicked_id] = useState(0);

    // Global State
    const imagesLocations = useSelector(GetImagesLocations);
    
    // Functions
    const onImageLocationClick = (id: number) => {
        setChangeImageModalShown(true);
        setClicked_id(id);
    };

    // Render elements
    let imagesLocationsRender = imagesLocations?.map((imagesLocation: ImagesLocationsType) => (
        <div onClick={() => onImageLocationClick(imagesLocation.id)}>
            {imagesLocation.place}
        </div>
    ))

    // Initial actions
    useEffect(() => {
        dispatch(getImagesLocationsThunk(access_token));
    }, [dispatch, access_token])


    return (
        <div className={styles.main_block}>
            <div className={styles.content_block}>
                {imagesLocationsRender}
                {clicked_id
                    ? <ChangeImagesModal
                        isModalOpen={changeImageModalShown} 
                        changeModalOpen={setChangeImageModalShown}
                        id={clicked_id}
                      />
                    : null
                }
                
            </div>
        </div>
    );
}

// Types
type ImagesLocationsType = {
    id: number;
    place: string;
};