import { Button, TextField } from "@mui/material";
import { useForm } from "react-hook-form";
import ReactModal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { addRegionLocationsThunk } from "../../../../Redux/Admin/Locations/LocationsThunk";
import { GetAccessToken } from "../../../../Redux/Auth/AuthSelector";
// Components
import { Exit } from "../../../Exit/Exit";

export const AddRegionModal: React.FC<PropsType> = (props) => {
    const dispatch = useDispatch();

    // Local state
    const {handleSubmit, register} = useForm<FormDataTypes>();
    const access_token = useSelector(GetAccessToken); 

    // Functions
    const onFormSubmit = (data: FormDataTypes) => {
        const preparedData = {
            absolute_name: data.absolute_name,
            main_location_id: props.id,
            translations: [
                {lang_id: 1, name: data.turkmen_name},
                {lang_id: 2, name: data.russian_name}
            ]
        };
        dispatch(addRegionLocationsThunk(preparedData, access_token));
    }

    return (
        <ReactModal isOpen={props.isModalOpen}>
            <form className='relative' onSubmit={handleSubmit(onFormSubmit)}>
                <Exit changeModalOpen={props.changeModalOpen} />
                <TextField
                    label='Абсолютное название'
                    {...register('absolute_name', {required: true}) }
                />
                <TextField 
                    label='На туркменском'
                    {...register('turkmen_name', {required: true}) }
                />
                <TextField 
                    label='На русском'
                    {...register('russian_name', {required: true})}
                />
                <Button variant='contained' color='success'>Добавить</Button>
                <Button variant='contained' color='error' onClick={() => {props.changeModalOpen(false)}}>Выйти</Button>
            </form>
        </ReactModal>
    );
}

// Types
type PropsType = {
    id: number;
    isModalOpen: boolean;
    changeModalOpen: (bool: boolean) => void;
};

type FormDataTypes = {
    absolute_name: string;
    turkmen_name: string;
    russian_name: string;
};
