import { useForm } from 'react-hook-form';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
// State
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
// Thunks
import { addMainLocationsThunk } from '../../../../Redux/Admin/Locations/LocationsThunk';
// Components
import { Exit } from '../../../Exit/Exit';
import { Button, TextField } from '@mui/material';
// Styles
import styles from './AddMainLocationModal.module.css';


export const AddMainLocationModal: React.FC<PropsType> = (props) => {
    const dispatch = useDispatch();

    // Local store
    const {handleSubmit, register} = useForm<FormDataType>();
    const access_token = useSelector(GetAccessToken);

    // Functions
    const onFormSubmit = (data: FormDataType) => {
        const preparedData = {
            absolute_name: data.absolute_name,
            translations: [
                {lang_id: 1, name: data.turkmen_name},
                {lang_id: 2, name: data.russian_name}
            ]
        };
        dispatch(addMainLocationsThunk(preparedData, access_token));
    }

    return (
        <Modal className={styles.modal} isOpen={props.isModalOpen}>
            <div className={styles.main_block}>
                <form className={styles.form} onSubmit={handleSubmit(onFormSubmit)}>
                    <Exit changeModalOpen={props.changeModalOpen}/>
                    <TextField
                        label='Абсолютное название'
                        {...register('absolute_name', {required: true}) }
                    />
                    <TextField
                        label='Название на туркменском'
                        {...register('turkmen_name', {required: true}) }
                    />
                    <TextField
                        label='Название на русском'
                        {...register('russian_name', {required: true})}
                    />
                    <Button className='cursor-pointer' variant='contained'>Добавить локацию</Button>
                    <Button className='cursor-pointer' variant='contained'
                        onClick={() => props.changeModalOpen(false)} color='error'>Выйти</Button>
                </form>
            </div>
        </Modal>
    );
};


// Types
type PropsType = {
    changeModalOpen: (bool: boolean) => void;
    isModalOpen: boolean;
};

type FormDataType = {
    absolute_name: string;
    turkmen_name: string;
    russian_name: string;
}