import { TextField } from "@mui/material";
import { useFormContext } from "react-hook-form";


export const EmailInput = () => {
    const { register, formState: { errors } } = useFormContext();
    return (
        <div>
            <TextField className='w-full' type='email' label='Email' {...register('email', {required: true})} />
            {errors.email?.type === 'required' && <p className='text-red-600'>Email обязателен</p>}
        </div>
    );
};