import { Link } from 'react-router-dom';
import styles from './Sidebar.module.css';
import {useHistory} from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { GetRoleId } from '../../Redux/Auth/AuthSelector';
import { AuthActions } from '../../Redux/Auth/AuthReducer';
import Cookies from 'js-cookie';
import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';
import RoomIcon from '@mui/icons-material/Room';
import ImageIcon from '@mui/icons-material/Image';
import ShowChartIcon from '@mui/icons-material/ShowChart';
import MergeTypeIcon from '@mui/icons-material/MergeType';
import MultipleStopIcon from '@mui/icons-material/MultipleStop';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import HomeWorkIcon from '@mui/icons-material/HomeWork';
import HomeIcon from '@mui/icons-material/Home';

export const Sidebar = () => {
    const history = useHistory();
    const location = history.location.pathname;
    const role_id = useSelector(GetRoleId);
    const dispatch = useDispatch();

    const onLogoutClick = () => {
        history.push('/auth');
        Cookies.remove('refresh_token');
        dispatch(AuthActions.logout())
    }


    return (
        <div className={styles.main_block}>
            {(role_id === 1) 
            ?
                <>
                    <Link to='/admin/operators' className={styles.links_initial}>
                        <div className={`${(location === '/admin/operators') ? styles.links_hover_block : 'hidden'}`} />
                        <ManageAccountsIcon className={`${(location === '/admin/operators') ? 'text-white' : ''} z-10`}/>
                        Операторы
                    </Link>
                    <Link to='/admin/specifications' className={styles.links_initial}>
                        <div className={`${(location === '/admin/specifications') ? styles.links_hover_block : 'hidden'}`} />
                        <MultipleStopIcon className={`${(location === '/admin/specifications') ? 'text-white' : ''} z-10`}/>
                        Спецификации
                    </Link>
                    <Link to='/admin/types' className={styles.links_initial}>
                        <div className={`${(location === '/admin/types') ? styles.links_hover_block : 'hidden'}`} />
                        <MergeTypeIcon className={`${(location === '/admin/types') ? 'text-white' : ''} z-10`} />
                        Типы
                    </Link>
                    <Link to='/admin/locations' className={styles.links_initial}>
                        <div className={`${(location === '/admin/locations') ? styles.links_hover_block : 'hidden'}`} />
                        <RoomIcon className={`${(location === '/admin/locations') ? 'text-white' : ''} z-10`}/>
                        Локации
                    </Link>
                    <Link to='/admin/pictures' className={styles.links_initial}>
                        <div className={`${(location === '/admin/pictures') ? styles.links_hover_block : 'hidden'}`} />
                        <ImageIcon className={`${(location === '/admin/pictures') ? 'text-white' : ''} z-10`}/>
                        Картинки
                    </Link>
                    <Link to='/admin/analytics' className={styles.links_initial}>
                        <div className={`${(location === '/admin/analytics') ? styles.links_hover_block : 'hidden'}`} />
                        <ShowChartIcon className={`${(location === '/admin/analytics') ? 'text-white' : ''} z-10`} />
                        Аналитика
                    </Link>
                </>
            :   
                <>
                    <Link className={styles.links_initial} to='/operator/unapproved'>
                        <div className={`${(location === '/operator/unapproved') ? styles.links_hover_block : 'hidden'}`} />
                        <HomeWorkIcon className={`${(location === '/operator/unapproved') ? 'text-white' : ''} z-10`}/>
                        Ожидающие</Link>
                    <Link className={styles.links_initial} to='/operator/vip'>
                        <div className={`${(location === '/operator/vip') ? styles.links_hover_block : 'hidden'}`} />
                        <HomeIcon className={`${(location === '/operator/vip') ? 'text-white' : ''} z-10`}/>
                        VIP</Link>
                    
                </>
            }
            <div onClick={onLogoutClick} className={styles.logout}>
                <ExitToAppIcon />
                Выйти
            </div>
        </div>
    );
};