import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Thunks
import { getTypesThunk } from "../../../Redux/Admin/Types/TypesThunk";
// useSelector
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Components
import { Header } from "../../../Components/Header/Header";
import { GetTypes } from "../../../Redux/Admin/Types/TypesSelector";
import { TypesType } from "../../../Redux/Admin/Types/TypesReducer";
import { PagesLoader } from "../../../Components/Loader/PagesLoader/PagesLoader";
// Styles
import styles from './Types.module.css';
import { AddSpecificationToType } from "../../../Components/Modal/TypesPage/AddSpecificationToType/AddSpecificationToType";
import AddIcon from '@mui/icons-material/Add';
import { TypeSpecificationsInfoModal } from "../../../Components/Modal/TypesPage/TypeSpecificationsInfoModal/TypeSpecificationsInfoModal";


// Types
type TranslationType = {
    language_id: number;
    name: string;
};

export const Types = () => {
    const dispatch = useDispatch();
    
    // Local store
    const [isLoading, setIsLoading] = useState(false);
    const [add_specification_to_type_modal_shown, set_add_specification_to_type_modal_shown] = useState(false);
    const [add_specification_modal_shown, set_add_specification_modal_shown] = useState(false);
    const [clicked_id, set_clicked_id] = useState(0);

    // Global store
    const access_token = useSelector(GetAccessToken);
    const types = useSelector(GetTypes);

    // Initial actions
    useEffect(() => {
        setIsLoading(true);
        dispatch(getTypesThunk(setIsLoading, access_token));
    }, [dispatch]);

    // Render items
    let TypesRender;
    if (types) {
        TypesRender = types.map((type: TypesType) => {
            return (
                <div className={styles.items_block}>
                    <div>{type.name}</div>
                    <div>{type.main_type}</div>
                    <div>{type.absolute_name}</div>
                    {type.destination
                        ? <div className='grid justify-self-center'> <img 
                            src={`${process.env.REACT_APP_BASE_URL}/${type.destination}`}
                            className='block w-20 h-20 mx-4'    
                        /> </div>
                        : <div>Surat ýok</div>
                    }
                    {type.translations
                        .filter((translation: TranslationType) => translation.language_id === 1)
                        .map((translation: TranslationType) => <div>{translation.name}</div>)
                    }
                    {type.translations
                        .filter((translation: TranslationType) => translation.language_id === 2)
                        .map((translation: TranslationType) => <div>{translation.name}</div>)
                    }
                    <AddIcon className={styles.addIcon} onClick={() => {set_clicked_id(type.id); set_add_specification_to_type_modal_shown(true)}}/>
                </div>
            );
        })
    }

    const headerItems = ['Id', 'Основной тип', 'Абсолютное название', 'Фотография', 'Назначение', 'На туркменском', 'На русском'];
    return (
        <div>
            {add_specification_to_type_modal_shown
                ? <TypeSpecificationsInfoModal
                    isModalOpen={add_specification_to_type_modal_shown}
                    changeModalOpen={set_add_specification_to_type_modal_shown}
                    nextModalOpen={set_add_specification_modal_shown}
                    id={clicked_id}
                />
                : null
            } 
            
            {add_specification_modal_shown
                ? <AddSpecificationToType
                    isModalOpen={add_specification_modal_shown}
                    changeModalOpen={set_add_specification_modal_shown}
                    id={clicked_id}
                    />
                : null
            }

            <Header headerItems={headerItems} />
            <div className={styles.content_block}>
                {isLoading
                    ? <PagesLoader />
                    : <> {TypesRender} </>
                }
            </div>
        </div>
    );
}